


















































































































































































































    import uniIcon from '../../components/uni-icon.vue'
	
    export default {
        components: {
           uniIcon
        },
        data() {
            return {
							currentTab: 0,
							showMask: false,
							showState: {
								top: false,
								middle: false,
								bottom: false,
								bottom1: false
							},
            }
        },
        methods: {
            bindChange: function (e) {	
							this.currentTab = e.detail.current;
						},
						swichNav: function(e){
							this.currentTab = e.currentTarget.dataset.current;
						},
						show: function(e) {
							var pos = e.currentTarget.dataset.position;
							switch (pos) {
								case 'top':
									this.activePop = 'top'
									break
								case 'bottom':
									this.activePop = 'bottom'
									break
								case 'bottom1':
									this.activePop = 'bottom1'
									break
								default:
									this.activePop = 'middle'
							}
							this.showMask = true
							this.showState[this.activePop] = true
						},
						hide: function() {
							this.showMask = false
							this.showState[this.activePop] = false
						},
        }
    }



